@import url(https://fonts.googleapis.com/css2?family=Varela&display=swap);
body{padding:0 5%;font-family:'Varela', sans-serif}

@media (max-width: 768px){.grid-view{grid-template-columns:1fr 1fr}.mobile-grid-design,.search-container,.search-result-container,.hotel-list,.sort-section{grid-template-columns:1fr !important;justify-items:center !important}.search-container input[type='button']{float:none !important;width:100% !important}.sort-section{height:auto !important;grid-row-gap:20px;row-gap:20px}.sort-section div{flex-direction:column}}.common-input-properties,input[type='text'],input[type='date'],input[type='button'],input[type='range']{border:2px solid gray;color:#000;outline:none;width:80%}input[type='button']{cursor:pointer}input[type='range']{-webkit-appearance:none;height:1px;border-radius:20px}input[type='range']:focus{outline:none}input[type='range']::-webkit-slider-runnable-track{width:100%;height:2px;cursor:pointer;background:gray}input[type='range']::-webkit-slider-thumb{height:30px;width:30px;border-radius:50%;background:gray;cursor:pointer;-webkit-appearance:none;margin-top:-13px}input[type='range']:focus::-webkit-slider-runnable-track{-webkit-appearance:none;background:gray;border-radius:50%}.App{color:#000;justify-content:center;min-height:100vh}

.empty-result{display:flex;justify-content:center}

.two-column{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:3%;-webkit-column-gap:3%;column-gap:3%}.flex-design{display:flex;flex-direction:column}.filter-section{margin-top:50px}.filter-section div{margin:10px 0}.filter-section div img{position:absolute;margin:10px 0 0 10px;width:20px;height:20px;opacity:.4}.filter-section div input[type='text']{border-radius:20px;height:34px;padding-left:35px;width:70%}

.two-column{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:3%;-webkit-column-gap:3%;column-gap:3%}.flex-design,.details-card{display:flex;flex-direction:column}.details-card{border:2px solid gray;margin:10px 0;width:100%}.details-card div{margin:10px 0;padding:0 10px}

.two-column,.sort-section,.hotel-list{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:3%;-webkit-column-gap:3%;column-gap:3%}.flex-design{display:flex;flex-direction:column}.sort-section{height:auto;align-items:center;margin-bottom:10px}.sort-section div{display:flex;justify-content:space-between;flex-wrap:wrap;grid-row-gap:20px;row-gap:20px}.sort-section div div{cursor:pointer;padding:10px 30px;border:2px solid gray}.sort-section .active{background-color:#c3c3c3;box-shadow:5px 5px 5px rgba(68,68,68,0.6)}

.search-container{display:grid;grid-template-columns:1fr 1fr 1fr;height:200px;align-items:center}.search-container span{margin-right:20px}.search-container input[type='date']{width:200px;height:30px;padding:0 10px}.search-container input[type='button']{width:50%;height:34px;padding:0 20px;background-color:transparent;outline:none;float:right;font-weight:bold}

.search-result-container{display:grid;grid-template-columns:250px 1fr}

.empty-result{display:flex;justify-content:center}.loader{width:100%;border-radius:4px;background:#fff}.loader .loading{position:relative;width:100%;height:10px;border-radius:4px;overflow:hidden}.loader .loading:after{content:'';position:absolute;left:0;width:0;height:100%;border-radius:4px;box-shadow:0 0 5px rgba(0,0,0,0.2);animation:load 5s infinite}@keyframes load{0%{width:0;background:#a28089}25%{width:40%;background:#a0d2eb}50%{width:60%;background:#ffa8b6}75%{width:75%;background:#d0bdf4}100%{width:100%;background:#494d5f}}@keyframes pulse{0%{background:#a28089}25%{background:#a0d2eb}50%{background:#ffa8b6}75%{background:#d0bdf4}100%{background:#494d5f}}

